























import { Component, Vue } from 'vue-property-decorator';
import { SelectItem } from '@/common/types';
import { getCodeListApi } from '@/utils/apis';
import MenuAuthorityList from '@/views/admin/menuauthority/components/MenuAuthorityList.vue';

@Component({
  name: 'MenuAuthority',
  components: {
    MenuAuthorityList,
  },
})
export default class extends Vue {
  authority: string | null = null;
  loading: boolean = false;
  AUTHORITY: SelectItem[] | null = null;

  mounted() {
    this.getCodeList();
  }

  async getCodeList() {
    this.loading = true;
    const data: SelectItem[] = await getCodeListApi('AUTHORITY');
    this.loading = false;
    this.AUTHORITY =
      data.filter(
        (value) =>
          ![Vue.$storage.get('authority'), 999].includes(parseInt(value.value)),
      ) || [];
  }
}
